@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.movieList{
    margin-bottom: 30px;
}

.movieList:hover .movieList--left,
.movieList:hover .movieList--right {
    opacity: 1;
}

.movieList h2 {
    margin: 0px 0px 0px 30px;
}

.icon {
    font-size: 50px;
}

.movieList--left,
.movieList--right {
    position: absolute;
    width: 40px;
    height: 122px;
    background-color: rgba(0,0,0,.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease .5s;
}

.movieList--left {
    left: 0;
}

.movieList--right {
    right: 0;
}

.movieList--listarea {
    overflow-x: hidden;
    padding-left: 30px;
}

.movieList--list{
    display: flex;
    transition: all .3s;
}

.movieList--item{
    height: 122px;
    width: 225px;
}

.movieList--item img{
    width: inherit;
    height: inherit;
    object-fit: cover;
    transform: scale(0.9);
    transition: all ease .3s;
    cursor: pointer;
}

.movieList--item img:hover {
    transform: scale(1);
}

@media (max-width:760px) {
    .movieList--left,
    .movieList--right {
        opacity: 1;
    }
}
.header{
    height: 100vh;
}

.header--vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.header--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;
}

.header--name {
    font-size: 60px;
    font-weight: bold;
}

.header--info {
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}

.header--info * {
    display: inline-block;
    margin-right: 15px;
}

.header--relevance {
    color: #46d369;
}

.header--description {
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}

.header--areaButtons{
    margin-top: 15px;
}

.header--areaButtons * {
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;
    border: 0;
    border-radius: 5px;
    margin-right: 10px;
    opacity: 1;
    transition: all ease .2s;
    cursor: pointer;
}

.header--areaButtons *:hover{
    opacity: .7;
}

.header--watchButton{
    background-color: #fff;
    color: #000;
}

.header--myListButton{
    background-color: #333;
    color: #fff;
}

.header--genres{
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

@media (max-width:760px){
    .header {
        height: 90vh;
    }
    .header--name {
        font-size: 40px;
    }
    .header--info {
        font-size: 16px;
    }
    .header--description {
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }
    .header--areaButtons * {
        font-size: 16px;
    }
    .header--genres{
        font-size: 14px;
    }
}
header{
    position: fixed;
    z-index: 999;
    top: 0;
    right: 0;
    left: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    background: transparent;
    transition: all ease .5s;
}

.background{
    background-color: #141414;
}

.menu--logo{
    height: 25px;
}

.menu--logo img{
    height: 100%;
}

.menu--user{
    height: 35px;
}

.menu--user img{
    height: 100%;
    border-radius: 3px;
}
*{
    box-sizing: border-box;
}
body{
    background-color: #111;
    color: #fff;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

.lists{
    margin-top: -150px;
}

footer{
    margin: 30px 0;
    text-align: center;
}

footer a{
    text-decoration: none;
    color: aqua;
}

footer a:hover{
    text-decoration: underline;
}

.loading {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 1000;
    background: #000;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loading img {
    height: 200px;
}
